* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100vh;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root{
  height: 100%;
}
:root{
    --main: hsl(142, 100%, 30%);
    --green: hsl(142, 100%, 40%);
    --red: hsl(0, 100%, 40%);
    --background: hsl(142, 2%, 95%);
    --text-dark: hsl(142, 0%, 15%);
    --text-light: hsl(142, 0%, 25%);
    --white-dark: hsl(142, 0%, 65%);
    
    --shadow-light: hsl(142, 0%, 80%);
    --shadow-dark: hsl(142, 0%, 60%);
}

.card{
    padding: 10px;
    box-shadow: 0px 3px 20px hsl(142, 0%, 80%);
    box-shadow: 0px 3px 20px var(--shadow-light);
    -webkit-transition-property: box-shadow;
    transition-property: box-shadow;
    -webkit-transition-duration: 250ms;
            transition-duration: 250ms;
    cursor: pointer;
    background-color: white;
}

.card:hover{
    box-shadow: 0px 3px 20px hsl(142, 0%, 60%);
    box-shadow: 0px 3px 20px var(--shadow-dark);
}
.icon {
    font-size: 18px;
    margin-left: 5px;
    display: inline-flex;
    align-items: center;
    margin-right: 5px;
}

.icon_size_big {
    font-size: 24px;
}

.icon_color_red {
    color: var(--red);
}

.icon_color_green {
    color: var(--green);
}

.icon_color_blue {
    color: blue;
}
.dashboard{
    height: 100%;
    display: flex;
    color: var(--text-dark);
}

.dashboard__content{
    width: 100%;
    padding: 10px;
    background-color: var(--text-light);
    padding-top: 90px;
}
.menu {
    width: 250px;
    background-color: var(--text-light);
    color: white;
    text-transform: uppercase;
    font-size: 14px;
}

.menu__header{
    background-color: var(--main);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90px;
}

.menu__item{
    padding: 15px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: var(--white-dark);
    -webkit-transition-property: color, background-color;
    transition-property: color, background-color;
    -webkit-transition-duration: 250ms;
            transition-duration: 250ms;
}

.menu__item_active_yes, .menu__item:hover{
    color: white;
    background-color: var(--text-dark);
}
.stat{
    height: 100%;
    width: 100%;
}



